<template>
   <button class="loginBtn loginBtn--google">
      <g-signin-button 
      :params="googleSignInParams"
      @success="onSignInSuccess"
      @error="onSignInError">
      使用 google 登入
      </g-signin-button>
   </button>
</template>



<script>

export default {
   name: 'GoogleLogin',
   props: {
      client_id: {
         type: String,
         default: ''
      }
   },
   data() {
      return {
         googleSignInParams: {
            client_id:  `${this.client_id}.apps.googleusercontent.com`
         }
      }
   },
   methods: {
      onSignInSuccess (googleUser) {
         
         const profile = googleUser.getBasicProfile();
         console.log(profile);
         var id_token = googleUser.getAuthResponse().id_token;
          console.log(id_token);
      },
      onSignInError (error) {
         console.log('OH NOES', error)
      }
   }
};
</script>


